const permittedHosts = ['teonistor.github.io', 'teonistor.herokuapp.com', 'teodor.nistor.uk'];
const interpretedHost = permittedHosts.includes(location.host) ? location.host : permittedHosts[0] + ' (on ' + location.host + ')';

// This isn't nice but it's better than something else
window.globals = {
  interpretedHost
}


import Vue from 'vue'
import App from './App.vue'
// import './registerServiceWorker'

import router from './plugins/router'
import vuetify from './plugins/vuetify';

import 'roboto-fontface/css/roboto/roboto-fontface.css'
import '@mdi/font/css/materialdesignicons.css'


/* API centralisation
 * - apiUrl - calls to the real backend (and activeComponents.json)
 * - dataUrl - static data stored in Github Pages (easily updated but easily browsed)
 * - localDataUrl - static data stored wherever the site is deployed (duplicated but safe from selective domain blocking)
 * - hiddenDataUrl - static data deployed only in Heroku (delayed access but cannot be browsed by the public)
 * External APIs
 * - githubApiUrl
 */
Vue.prototype.$apiUrl = '/api';
Vue.prototype.$dataUrl = 'https://teodor.nistor.uk/data';
Vue.prototype.$localDataUrl = '/data';
Vue.prototype.$hiddenDataUrl = 'https://teonistor.herokuapp.com/documents';
Vue.prototype.$githubApiUrl = 'https://api.github.com'

Vue.config.productionTip = false;

new Vue({
  router,
  vuetify,
  render: h => h(App)
}).$mount('#app')
