<script>
import axios from 'axios';

export default {
  name: 'App',

  data: () => ({
    lightTheme: false,
    activeComponents: []
  }),

  computed: {
    backendPresent () {
      return !this.activeComponents.includes('Full');
    },

    loggedIn () {
      return this.activeComponents.includes('Logout');
    },

    interpretedHost () {
      return window.globals.interpretedHost;
    }
  },

  watch: {
    lightTheme: function(light) {
      this.$vuetify.theme.dark = !light;
    }
  },

  mounted () {
    axios({ method: 'GET', url: this.$apiUrl + '/activeComponents.json' })
      .then(result => this.activeComponents = result.data,
            error  => { console.error(error); });
    }
};
</script>
